let productItems = document.querySelectorAll('.products-item');

for (let i = 0; i < productItems.length; i++) {
	let productItem = productItems[i];

	let showProductBtn = document.querySelector('.js-show-all-products');

	let showHiddenProducts = () => {
		if (productItem.classList.contains('products-item--hidden')) {
			productItem.classList.remove('products-item--hidden');
			setTimeout(() => {
				showProductBtn.remove();
			}, 500);
		}
	};

	showProductBtn.addEventListener('click', showHiddenProducts);
}
