let menuBtn = document.querySelector('.js-burger-btn');

let	menuBurger = menuBtn.querySelector('.page-header__burger');

let	topHeaderNavList = document.querySelector('.js-top-header-nav');

// let	topHeaderNavLinks = topHeaderNavList.querySelectorAll('.js-nav-link');

let hideShowNav = () => {
	if (menuBurger.classList.contains('active')) {
		menuBurger.classList.remove('active');
		topHeaderNavList.classList.remove('active');
	} else {
		menuBurger.classList.add('active');
		topHeaderNavList.classList.add('active');
	}
};

menuBtn.addEventListener('click', hideShowNav);

